.signup{
    height: calc(120vh - 40px);
    background-image:linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ), url("../../assets/images/signup.jpg");
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.signupTitle{
    padding:5px;
    text-align:center;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;
    color:darken(#e5e5e5, 50%);
    border-bottom:solid 1px #e5e5e5;
    font-size: 50px;
}

.signupForm {
    margin-top: 20px;
    width: 30%;
    display: flex;
    flex-direction: column;
    background: #fff;
    box-shadow: 0px 11px 28px 0px rgba(0, 0, 0, 0.15);
  }

  label{
    color: #666;
    font-weight: 600;
    font-size: 13px;
    text-transform: uppercase;
    margin-bottom: 3px;
    font-family: 'Open Sans', sans-serif;
    margin: 10px 2px;
  }

  .signupInput {
    background-color: white;
    padding: 5px 15px 10px 15px;
    border: 2px solid transparent;
    border-bottom: 2px solid #e5e5e5;
    appearance: unset;     
    outline: none;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    font-weight: 700;
    color: #333;
    box-sizing: border-box;
  }

  .signupInput:focus {
    border-bottom: 2px solid #2bb33e;
  }

  .signupError{
    color: red;
    text-align: left;
    height: 1px;
    font-size: x-small;
    margin-bottom: 14px;
    padding: 5px;
  }

  .signupButton {
    width: 50%;
    margin:1.5em 6em ;
    border: none;
    border-radius: 10px;
    padding: 15px;
    background-color: lightcoral;
    color: white;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    font-weight: 700;
  }

